<template>
  <!-- App Header -->
  <header>
    <nav class="uk-navbar-container" uk-navbar>
      <div class="uk-navbar nav-1">
        <router-link :to="`/${$i18n.locale}`" tag="a" class="uk-navbar-item uk-logo">
          <img src="@/assets/imgs/logo.png" alt="Website logo" width="45px" uk-img>
        </router-link>
        <ul class="uk-navbar-nav uk-visible@s">
          <li>
            <router-link :to="`/${$i18n.locale}`" tag="a">{{ $t('appHeader.home') }}</router-link>
          </li>
          <li>
            <router-link :to="`/${$i18n.locale}/About`" tag="a">{{ $t('appHeader.about') }}</router-link>
          </li>
          <li>
            <router-link :to="`/${$i18n.locale}/Join`" tag="a">{{ $t('appHeader.join') }}</router-link>
          </li>
          <li>
            <router-link :to="`/${$i18n.locale}/Contact`" tag="a">{{ $t('appHeader.contact') }}</router-link>
          </li>
        </ul>
      </div>

      <div class="uk-navbar nav-2">
        <!-- This to open side menu -->
        <a class="uk-link toggle-sidenav uk-hidden@s" uk-icon="menu" href="#" uk-toggle="target: #offcanvas-nav-primary"></a>
        <ul class="uk-navbar-nav uk-visible@s">
          <li>
            <a class="lan_ar" @click.prevent="setLocale('ar')" v-show="!lan_ar" @click="lan_ar = true">{{ $t('appHeader.ar') }}</a>
          </li>
          <li>
            <a class="lan_en" @click.prevent="setLocale('en')" v-show="lan_ar" @click="lan_ar = false">{{ $t('appHeader.en') }}</a>
          </li>
          <li>
            <a target="_blank" href="https://play.google.com/store/apps/details?id=com.orid.user">
              <img src="@/assets/imgs/app/google.png" alt="Play Store">
            </a>
          </li>
          <li>
            <a href="#">
              <img src="@/assets/imgs/app/apple.png" alt="App Store">
            </a>
          </li>
        </ul>
      </div>
    </nav>

    <!-- Side navbar -->
    <div class="sidenav" id="offcanvas-nav-primary" uk-offcanvas="mode: push; overlay: true; flip: true">
      <div class="uk-offcanvas-bar uk-flex uk-flex-column">
        <button class="uk-offcanvas-close" type="button" uk-close></button>
        <ul class="uk-nav uk-nav-primary uk-nav-center uk-margin-auto-vertical">
          <li>
            <router-link :to="`/${$i18n.locale}`" tag="a" class="uk-offcanvas-close">{{ $t('appHeader.home') }}</router-link>
          </li>
          <li>
            <router-link :to="`/${$i18n.locale}/About`" tag="a" class="uk-offcanvas-close">{{ $t('appHeader.about') }}</router-link>
          </li>
          <li>
            <router-link :to="`/${$i18n.locale}/Join`" tag="a" class="uk-offcanvas-close">{{ $t('appHeader.join') }}</router-link>
          </li>
          <li>
            <router-link :to="`/${$i18n.locale}/Contact`" tag="a" class="uk-offcanvas-close">{{ $t('appHeader.contact') }}</router-link>
          </li>
          <li class="uk-nav-divider"></li>
          <li class="uk-parent">
            <ul class="uk-nav-sub">
              <li>
                <a class="lan_ar uk-offcanvas-close" @click.prevent="setLocale('ar')" v-show="!lan_ar" @click="lan_ar = true">{{ $t('appHeader.ar') }}</a>
              </li>
              <li>
                <a class="lan_en uk-offcanvas-close" @click.prevent="setLocale('en')" v-show="lan_ar" @click="lan_ar = false">{{ $t('appHeader.en') }}</a>
              </li>
              <li>
                <a target="_blank" href="https://play.google.com/store/apps/details?id=com.orid.user">
                  <img src="@/assets/imgs/app/google.png" alt="Play Store">
                </a>
              </li>
              <li>
                <a href="#">
                  <img src="@/assets/imgs/app/apple.png" alt="App Store">
                </a>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </header>
</template>

<script>
  export default {
    name: "AppHeader",
    data: function () {
      return {
        lan_ar: true
      }
    },
    methods: {
      setLocale (locale) {
        this.$i18n.locale = locale;
        this.$router.push({
          params: {lang: locale}
        });
        if (locale === 'ar') {
          document.querySelector('body').classList.add('rtl');
        } else {
          document.querySelector('body').classList.remove('rtl');
        }
      }
    },
    mounted () {
      document.addEventListener("DOMContentLoaded", () => {
        if (this.$i18n.locale === 'ar') {
           document.querySelector('body').classList.add('rtl');
           this.lan_ar = true
        } else {
          document.querySelector('body').classList.remove('rtl');
           this.lan_ar = false
        }
      });
    },
    updated () {
      if (this.$i18n.locale === 'ar') {
          document.querySelector('body').classList.add('rtl');
          this.lan_ar = true
      } else {
        document.querySelector('body').classList.remove('rtl');
          this.lan_ar = false
      }
    }
  }
</script>