<template>
  <main>
    <!-- Intro -->
    <section class="uk-section uk-section-default uk-padding-remove page-intro">
      <div class="uk-cover-container uk-height-medium">
        <img src="@/assets/imgs/cover2.jpg" uk-cover>
      </div>
      <div class="uk-overlay uk-position-cover">
        <div class="uk-container">
          <h2 v-html="$t('contactUs.title')"/>
        </div>
      </div>
    </section>

    <!-- Contact US -->
    <section class="uk-section uk-section-default contact-us">
      <div class="uk-container">
        <div class="section-body">
          <div class="social-contact">
            <p>
              {{ $t('contactUs.description') }}
            </p>
            <ul class="social-links">
              <li class="fb">
                <a href="#" uk-icon="icon:facebook; ratio: 0.9"></a>
              </li>
              <li class="ig">
                <a href="#" uk-icon="icon:instagram; ratio: 0.9"></a>
              </li>
              <li class="wb">
                <a href="#" uk-icon="icon:whatsapp; ratio: 0.9"></a>
              </li>
            </ul>
          </div>
          <form @submit.prevent="send_message" v-if="!formSuccess" class="contact-form uk-grid-smal" uk-grid>
            <div class="uk-width-1-2">
              <div class="uk-inline">
                <span class="uk-form-icon" uk-icon="icon: user"></span>
                <input class="uk-input" type="text" :placeholder="$t('contactUs.form.fName')" v-model="firstName"/>
              </div>
              <span class="error" v-if="!$v.firstName.required && $v.firstName.$dirty">{{ $t('contactUs.errors.fName') }}</span>
              <span class="error" v-if="!$v.firstName.maxLength && $v.firstName.$dirty">{{ $t('contactUs.errors.maxChars') }} {{ $v.firstName.$params.maxLength.max }}</span>
            </div>
            <div class="uk-width-1-2">
              <div class="uk-inline">
                <span class="uk-form-icon" uk-icon="icon: user"></span>
                <input class="uk-input" type="text" :placeholder="$t('contactUs.form.lName')" v-model="lastName"/>
              </div>
              <span class="error" v-if="!$v.lastName.required && $v.lastName.$dirty">{{ $t('contactUs.errors.lName') }}</span>
              <span class="error" v-if="!$v.lastName.maxLength && $v.lastName.$dirty">{{ $t('contactUs.errors.maxChars') }} {{ $v.lastName.$params.maxLength.max }}</span>
            </div>
            <div class="uk-width-1-1">
              <div class="uk-inline">
                <span class="uk-form-icon" uk-icon="icon: mail"></span>
                <input class="uk-input" type="email" :placeholder="$t('contactUs.form.email')" v-model="email"/>
              </div>
              <span class="error" v-if="!$v.email.required && $v.email.$dirty">{{ $t('contactUs.errors.email') }}</span>
              <span class="error" v-if="!$v.email.email">{{ $t('contactUs.errors.notEmail') }}</span>
            </div>
            <div class="uk-width-1-1">
              <div class="uk-inline">
                <span class="uk-form-icon" uk-icon="icon: receiver"></span>
                <input class="uk-input" type="number" :placeholder="$t('contactUs.form.phone')" v-model="phone"/>
              </div>
              <span class="error" v-if="!$v.phone.required && $v.phone.$dirty">{{ $t('contactUs.errors.phone') }}</span>
              <span class="error" v-if="(!$v.phone.numeric || !$v.phone.minLength) && $v.phone.$dirty">{{ $t('contactUs.errors.notPhone') }}</span>
            </div>
            <div class="uk-width-1-1">
              <textarea class="uk-textarea" rows="5" :placeholder="$t('contactUs.form.message')" v-model="description"></textarea>
              <span class="error" v-if="!$v.description.required && $v.description.$dirty">{{ $t('contactUs.errors.message') }}</span>
              <span class="error" v-if="!$v.description.maxLength && $v.description.$dirty">{{ $t('contactUs.errors.maxChars') }} {{ $v.description.$params.maxLength.max }}</span>
            </div>

            <div class="actions uk-width-1-1">
              <button class="uk-button uk-button-secondary btn-round" type="submit">
                <b>{{ $t('contactUs.form.send') }}</b>
              </button>
            </div>
          </form>
          <div class="message-sent" v-if="formSuccess">
            <span class="icon" uk-icon="icon:happy; ratio: 7"></span>
            <p>{{ $t('contactUs.form.success') }}</p>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
  import axios from "axios";
  window.axios = require('axios');
  import {required, maxLength, minLength, email, numeric} from "vuelidate/lib/validators";
  export default {
    name: "Contact",
    data: function () {
      return {
        firstName: "",
        lastName: "",
        email: "",
        phone: "",
        description: "",
        formSuccess: false
      }
    },
    validations: {
      firstName: {
        required,
        maxLength: maxLength(15)
      },
      lastName: {
        required,
        maxLength: maxLength(15)
      },
      email: {
        required,
        email
      },
      phone: {
        required,
        numeric,
        minLength: minLength(11)
      },
      description: {
        required,
        maxLength: maxLength(2000)
      }
    },
    methods: {
      send_message () {
        this.$v.$touch();
        if(!this.$v.$invalid) {
          axios.post(
            `http://75.119.157.225:1100/v1/ContactUs`,
            {
              name: this.firstName + ' ' + this.lastName,
              email: this.email,
              title: this.phone,
              description: this.description
            }
          );
          this.formSuccess = true
        }
      }
    },
  }
</script>